.Start div .ef::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ff2f2f;
  -webkit-box-shadow: 0 0 10px #ff2f2f, 0 0 20px #ff2f2f, 0 0 30px #ff2f2f, 0 0 40px #ff2f2f, 0 0 50px #ff2f2f, 0 0 60px #ff2f2f, 0 0 70px #ff2f2f, 0 0 80px #ff2f2f, 0 0 90px #ff2f2f, 0 0 100px #ff2f2f;
